import { mapState } from 'vuex'
import venueItem from '../../components/venueItem.vue'
export default {
    data () {
        return {
            venueArr: []
        }
    },
    created() {
        this.getArrData()
    },
    computed: {
        ...mapState(['locationObj'])
    },
    components: {
        venueItem
    },    
    methods: {
        async getArrData () {
            let params = {
                page: 1,
                pageSize: 100,
                city_id: this.locationObj.id
            }
            const res = await this.$api.getVenueList(params)
            if (res.code === 0) {
                this.venueArr = res.data.data
            }
        }
    }
}
